* {
  background-color: #7ca88c;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  background-color: #eceaa0;
  color: #35323b;
  border-color: #bed24c;
}

button:hover {
  background-color: #eceaa0;
  color: #35323b;
  border-color: #859032;
}
.wrapper {
  color: #ffffff;
  width: 100vw;
  height: 100vh;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game-name {
  font-family: cursive;
  font-size: 7vw;
  text-align: center;
  line-height: 1.1em;
}

p {
  color: #35323b;
}

.login-form {
  display: flex;
  padding: 10px;
  margin: 10px;
  flex-direction: column;
  align-items: center;
}

.drop-down {
  background-color: #eceaa0;
  color: #35323b;
  border-color: #859032;
}


.question {
  border: 1px solid black;
  border-radius: 5px;
  width: 25em;
  margin: 10px;
  padding: 10px;
}
nav {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 10px;
  border-bottom: 2px solid #35323b;
}

nav div {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
}

nav a {
  padding: 0 10px;
  text-decoration: none;
  color: #35323b;
}


nav a:hover {
  color: #eceaa0;
}

nav p {
  flex: 1 1;
  text-align: right;
}
.buttons {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.questions {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}
.main {
  display: flex;
  justify-content: center;
}

.unanswered-question {
  border: 1px solid black;
  border-radius: 5px;
  width: 25em;
  margin: 10px;
  padding: 10px;
}

.main {
  display: flex;
  justify-content: center;
}

.answered-question {
  border: 1px solid black;
  border-radius: 5px;
  width: 25em;
  margin: 10px;
  padding: 10px;
}

.answer {
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
}
.new-question {
  border: 1px solid black;
  border-radius: 5px;
  width: 25em;
  margin: 10px;
  padding: 10px;
}

.new-question p {
  padding: 5px;
}

.new-question input {
  background-color: #eceaa0;
  padding: 5px;
}
.users {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.user {
  border: 1px solid black;
  border-radius: 5px;
  width: 25em;
  margin: 10px;
  padding: 10px;
}
